import React, { useRef, useEffect, useState } from 'react';
import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import { colors } from './assets/colors'
import { LinearGradient } from 'expo-linear-gradient';
import { BlurView } from 'expo-blur';
import Ionicons from '@expo/vector-icons/Ionicons';
import { useFonts } from 'expo-font';
import { useHover } from 'react-native-web-hooks';

const today = new Date()

export default function App() {

	const [state, setState] = useState(

		{
			Date: today,
			Year: 2022,
			MonthString: "May",
			MonthDates: [
				["","","","","","",""],
				["","","","","","",""],
				["","","","","","",""],
				["","","","","","",""],
				["","","","","","",""],
			]
		}

	)

	useEffect(() => {
		
		CheckMonthDate()

	},[])

	const CheckMonthDate = () => {

		state.Year = state.Date.getFullYear()

		state.MonthString = state.Date.toLocaleString('en-En', { month: 'short' })

		state.Date.setDate(1)

		state.MonthDates = [
			["","","","","","",""],
			["","","","","","",""],
			["","","","","","",""],
			["","","","","","",""],
			["","","","","","",""],
		]

		state.MonthDates[0][state.Date.getDay()] = "1"

		const i = new Date(state.Date.getFullYear(),state.Date.getMonth()+1,0)
		const MaxDay = i.getDate()

		for (let i = 0; i < 5; i ++) {

			for (let j = 0; j < 7; j ++) {

				if (state.MonthDates[i][j] < MaxDay && state.MonthDates[i][j] != "") {

					if (j != 6) {

						state.MonthDates[i][j + 1] = (Number(state.MonthDates[i][j]) + 1).toString()

					} else if (j == 6) {

						if (i != 4) {

							state.MonthDates[i + 1][0] = (Number(state.MonthDates[i][j]) + 1).toString()

						}

					}

				}

			}

		}

		console.log(state.MonthDates)

	}

	const NextMonth = () => {

		if (state.Date.getMonth() < 11) {

			state.Date.setMonth(state.Date.getMonth() + 1)
			
			CheckMonthDate()

			setState({...state,Date: state.Date, MonthString: state.Date.toLocaleString('en-En', {month:'short'}), MonthDates: state.MonthDates})

		} else if (state.Date.getMonth() == 11) {

			state.Date.setFullYear(state.Date.getFullYear() + 1)
			state.Date.setMonth(0)

			CheckMonthDate()

			setState({...state,Date: state.Date, MonthString: state.Date.toLocaleString('en-En', {month:'short'}), Year: state.Date.getFullYear(), MonthDates: state.MonthDates})

		}

	}

	const LastMonth = () => {

		if (state.Date.getMonth() > 0) {

			let s = state

			s.Date.setMonth(s.Date.getMonth() - 1)

			CheckMonthDate()

			setState({...state,Date: s.Date, MonthString: state.Date.toLocaleString('en-En', {month:'short'}), MonthDates: state.MonthDates})

		} else if (state.Date.getMonth() == 0) {

			let s = state

			s.Date.setFullYear(s.Date.getFullYear() - 1)
			s.Date.setMonth(11)

			CheckMonthDate()

			setState({...state,Date: s.Date, MonthString: state.Date.toLocaleString('en-En', {month:'short'}), Year: state.Date.getFullYear(), MonthDates: state.MonthDates})

		}

	}

	const [loaded] = useFonts({

		Consolas: require('./assets/fonts/CONSOLA.TTF')

	})

	if (!loaded) {

		return null

	}
	
	const MonthBtn = () => {
		
		return (
			<View style={styles.CalendarRow}>

			<BlurView intensity={1} style={{borderRadius:10, marginTop: 20}}>

				<TouchableOpacity style={styles.CalBtn} onPress={() => LastMonth()}>

					<Ionicons name="arrow-back-outline" size={30} style={{color: colors.Cultured}}/>

				</TouchableOpacity>

			</BlurView>

			<Text style={[styles.CalMonthYear,{marginTop: 28}]}>{state.MonthString + " " + state.Year}</Text>

			<BlurView intensity={1} style={{borderRadius:10, marginTop: 20}}>

				<TouchableOpacity style={styles.CalBtn} onPress={() => NextMonth()}>

					<Ionicons name="arrow-forward-outline" size={30} style={{color: colors.Cultured}}/>

				</TouchableOpacity>

			</BlurView>

			</View>
			
		)

	}

	const DateOfMonth = (prop) => {

		return (

			<BlurView intensity={23} style={{borderRadius:5, marginTop: 20, margin: 5}}>
				
				<View style={styles.DateOfMonth}>
				
					<Text style={styles.DateOfMonthText}>{prop.Text}</Text>

				</View>
				
			</BlurView>

		)

	}

	const Day = (props) => {

		const ref = useRef(null)

		const isHovered = useHover(ref)
		
		if (state.MonthDates[props.week][props.day] == "") {

			return (

				<Text style={[styles.DayText]}></Text>

			)

		}

		return (

			<Text ref={ref} style={[styles.DayText, isHovered && {backgroundColor: `${colors.Cultured}99`, color: colors.ResinBlack, borderColor: 'rgba(0,0,0,0.2)', borderWidth: 1}]}>{state.MonthDates[props.week][props.day]}</Text>

		)

	}

	const Week = (props) => {

		return (

			<View style={styles.CalendarRow}>

				<Day week={props.week} day={0}></Day>
				<Day week={props.week} day={1}></Day>
				<Day week={props.week} day={2}></Day>
				<Day week={props.week} day={3}></Day>
				<Day week={props.week} day={4}></Day>
				<Day week={props.week} day={5}></Day>
				<Day week={props.week} day={6}></Day>
				
			</View>
			
		)

	}

	return (

		<View style={styles.container}>
			
			<LinearGradient colors={[colors.ParadisePink, colors.Jonquil]} end={{x:-0.4,y:1.8}} style={styles.Prop1}/>
			
			<LinearGradient colors={[colors.CornflowerBlue, colors.ShamrockGreen]} end={{x:0.2,y:-0.7}} style={styles.Prop2}/>

			<BlurView intensity={35} style={{borderRadius:40}}>

				<View style={styles.Box}>

					<MonthBtn/>
					
					<View style={styles.CalendarRow}>

						<DateOfMonth Text={"S"}/>
						<DateOfMonth Text={"M"}/>
						<DateOfMonth Text={"T"}/>
						<DateOfMonth Text={"W"}/>
						<DateOfMonth Text={"T"}/>
						<DateOfMonth Text={"F"}/>
						<DateOfMonth Text={"S"}/>

					</View>

					<Week week={0}/>
					<Week week={1}/>
					<Week week={2}/>
					<Week week={3}/>
					<Week week={4}/>

				</View>
			
			</BlurView>

			<StatusBar style="auto" />

		</View>

	);

}

const styles = StyleSheet.create({
	container: {
		flex: 1,
		backgroundColor: colors.ResinBlack,
		alignItems: 'center',
		justifyContent: 'center',
	},
	Prop1: {
		backgroundColor: colors.Cultured,
		width: 350,
		height: 350,
		borderRadius: 350 / 2,
		position: 'absolute',
		transform: [
			{translateX:-250},
			{translateY:-180}
		]
	},
	Prop2: {
		backgroundColor: colors.Cultured,
		width: 270,
		height: 270,
		borderRadius: 270 / 2,
		position: 'absolute',
		transform: [
			{translateX:170},
			{translateY:120}
		]
	},
	Box: {
		backgroundColor: `${colors.Cultured+"12"}`,
		width: 400,
		height: 440,
		borderRadius: 40,
		shadowColor: colors.ResinBlack,
		shadowRadius: 45,
		shadowOpacity: 0.53,		
		borderWidth: 1,
		borderColor: 'rgba(255,255,255,0.51)',
		borderRightColor: 'rgba(255,255,255,0.49)',
		borderBottomColor: 'rgba(255,255,255,0.49)',
	},
	CalendarRow: {
		paddingLeft: 20,
		paddingRight: 20,
		flexDirection: 'row',
	},
	CalBtn: {
		width: (400 - 40) / 7,
		height: (400 - 40) / 7,
		borderRadius: 10,
		justifyContent: 'center',
		alignItems: 'center',
		shadowColor: colors.ResinBlack,
		shadowRadius: 10,
		shadowOpacity: 0.23,
		borderColor: 'rgba(255,255,255,0.23)',
		borderWidth: 0.2
	},
	CalMonthYear: {
		color: colors.Cultured,
		fontSize: 30,
		fontWeight: 'bold',
		width: (400 - 40) / 7 * 5,
		justifyContent: 'center',
		alignItems: 'center',
		textAlign: 'center',
		fontFamily: 'Consolas'
	},
	DateOfMonth: {
		borderRadius: 5,
		width: ((400 - 40) / 7 ) - 10,
		height: ((400 - 40) / 7 ) - 10,
		justifyContent: 'center',
		alignItems: 'center',
		shadowColor: colors.ResinBlack,
		shadowRadius: 10,
		shadowOpacity: 0.32,
		borderColor: 'rgba(255,255,255,0.23)',
		borderWidth: 0.2
	},
	DateOfMonthText: {
		color: colors.Cultured,
		fontSize: 20,
		fontWeight: 'bold',
		paddingBottom: 3
	},
	DayText: {
		color: colors.Cultured,
		fontSize: 20,
		fontWeight: 'bold',
		width: ((400 - 40) / 7 ) - 10,
		height: ((400 - 40) / 7 ) - 10,
		justifyContent: 'center',
		alignItems: 'center',
		textAlign: 'center',
		textAlignVertical: 'center',
		marginTop: 10,
		margin: 5,
		paddingTop: 5,
		borderRadius: 5
	}
});
